body {
  background-color: #333;
  padding: 20px;
  color: #f8f1f1;
}

.header-container {
  background: #333;
  width: 100%;
  position: fixed;
  top: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  left: 0;
  border-bottom: solid 1px #ffa62b;
}

.header-brand {
  font-size: 35px;
  margin: 10px;
  color: #f8f1f1;
  font-family: Arial, Helvetica, sans-serif;
}

.header-create {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: #ffa62b;
  margin: 10px;
}

.header-add-course {
  margin: 10px;
  border: 0;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  background-image: linear-gradient(95deg, #bf941fad, #ffa500);
  box-shadow: 6px 6px 8px 0 rgba(0, 0, 0, 0.16), -4px -4px 6px 0 rgba(100, 100, 100, 0.29);
  font-size: x-large;
  color: #333;
}

.content-container {
  background-color: #333;
  display: flex;
  margin-top: 60px;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.content-container .content-course {
  width: 100px;
  height: 30px;
  border-radius: 5px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #f8f1f1;
  margin: 10px 5px 5px 10px;
  box-shadow: 6px 6px 8px 0 rgba(0, 0, 0, 0.16), -4px -4px 6px 0 rgba(100, 100, 100, 0.29);
}

.content-container .content-course:hover {
  box-shadow: 5px 5px 5px rgba(68, 68, 68, 0.6);
  border: solid 2px grey;
}

@media (max-width: 767px) {
  .content-container {
    width: 100%;
  }
}

.hide {
  display: none;
}

.adder-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
}

.adder-container .adder-button {
  width: 40px;
  height: 40px;
  border: solid 2px #ffa62b;
  border-radius: 50%;
  color: #f8f1f1;
  background-color: transparent;
  box-shadow: 12px 12px 16px 0 rgba(0, 0, 0, 0.11), -8px -8px 12px 0 rgba(0, 0, 0, 0.3);
}

.adder-container .adder-inputs {
  display: flex;
  flex-direction: column;
  width: 50%;
  align-items: center;
}

.adder-container .adder-inputs .adder-numbers {
  display: flex;
  justify-content: center;
}

.adder-container .adder-inputs input {
  background-color: transparent;
  border-radius: 10px;
  padding: 5px 10px 5px 10px;
  margin: 5px;
  color: #f8f1f1;
}

.adder-container .adder-inputs button {
  margin: 10px;
  border: 0;
  border-radius: 5px;
  width: inherit;
  height: 30px;
  background: #ffa62b;
  font-size: large;
  color: #333;
}

.assessment-container {
  margin: 20px;
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  background-color: #1313131c;
  overflow-y: scroll;
  height: 510px;
  box-shadow: inset 2px 2px 3px 0 rgba(0, 0, 0, 0.2), inset -2px -2px 3px 0 rgba(255, 255, 255, 0.068);
}

.assessment-container .assessment-completion {
  margin-top: 10px;
  text-align: center;
}

.assessment-container .assessment-completion-bar {
  height: 3px;
  background-color: rgba(253, 253, 253, 0.671);
  margin-top: 10px;
  width: 100%;
}

.assessment-container .assessment-data {
  padding: 20px 30px 20px 30px;
  border-radius: 10px;
  box-shadow: 6px 6px 8px 0 rgba(0, 0, 0, 0.16), -4px -4px 6px 0 rgba(100, 100, 100, 0.29);
  height: 100px;
  margin: 10px 20px 10px 20px;
}

.assessment-container .assessment-single {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.assessment-container .assessment-remove {
  border: 0;
  border-radius: 50%;
  height: 40px;
  width: 40px;
  background: #ffa62b;
  font-size: x-large;
  color: #333;
}

.overview-container {
  padding: 10px 30px;
  margin: 20px;
  justify-content: space-between;
  display: flex;
  flex-direction: row;
  border-radius: 15px;
  box-shadow: 6px 6px 8px 0 rgba(0, 0, 0, 0.16), -4px -4px 6px 0 rgba(100, 100, 100, 0.29);
}

.overview-container .overview-header {
  font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
  background-image: linear-gradient(95deg, #bf941fad, #ffa500);
  background-size: 100%;
  background-repeat: repeat;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.overview-container .overview-mark {
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 100;
  margin: auto 0;
  font-size: 40px;
  background-image: linear-gradient(95deg, #ffffffad, #5e5e5e);
  background-size: 100%;
  background-repeat: repeat;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.overview-container .overview-mark .overview-gpa {
  width: auto;
  font-size: 15px;
  font-style: italic;
  font-weight: 500;
  border-radius: 20px;
  padding: 5px 10px;
  box-shadow: inset 2px 2px 3px 0 rgba(0, 0, 0, 0.2), inset -2px -2px 3px 0 rgba(255, 255, 255, 0.068);
}

.overview-container .overview-mark .awesome {
  border: solid 1px rgba(21, 112, 21, 0.774);
}

.overview-container .overview-mark .good {
  border: solid 1px #5e8000;
}

.overview-container .overview-mark .okay {
  border: solid 1px yellow;
}

.overview-container .overview-mark .bad {
  border: solid 1px #bd3c2f;
}

.notification-container {
  margin-top: 60px;
}

body {
  box-shadow: inset 2px 2px 3px 0 rgba(0, 0, 0, 0.2), inset -2px -2px 3px 0 rgba(255, 255, 255, 0.068);
}

